.account-wizard {
    display: grid;
    grid-template-rows: 13rem minmax(10rem, 1fr);
    height: calc(100% - 5.7rem);
    position: relative;
    width: 100%;
    @include desktop {
        transform: translateY(0);
        &--deriv-crypto {
            grid-template-rows: 0 !important;
        }
        .currency-list__items {
            &__is-crypto {
                margin-bottom: 9rem;
            }
        }
        .dc-input__label {
            background-color: var(--fill-normal) !important;
        }
        .dc-modal-footer {
            background-color: var(--general-main-1);
            z-index: 1;
        }
    }
    @include mobile {
        grid-template-rows: 1fr auto;
        overflow: hidden;

        &--set-currency {
            grid-template-rows: 1fr 7rem;
        }
    }

    &.add-or-manage {
        grid-template-rows: 1fr 1fr;

        & .dc-form-submit-button {
            padding: 2rem;
        }
        @include desktop {
            & form {
                grid-template-rows: auto;
            }
        }
        @include mobile {
            grid-template-rows: 1fr 1fr;
            overflow-y: auto;
            overflow-x: hidden;
        }
    }
    & > h2 {
        text-transform: none;
        margin-top: 2.4rem;
        @include typeface(--title-center-bold-black);
        color: var(--text-less-prominent);
        @include mobile {
            margin-top: 0.8rem;
            font-size: 1.2rem;
        }
    }
    & > h3 {
        @include typeface(--small-center-normal-black);
        text-transform: none;
        color: var(--text-general);
        margin-bottom: 1.6rem;
    }
    form {
        width: 100%;
        height: 100%;

        @include tablet-up {
            display: grid;
            grid-template-rows: minmax(20rem, 1fr);
        }

        .currency-selector {
            &__radio-group {
                display: flex;
                flex-direction: column;
                flex-grow: 1;
                justify-content: center;
                align-items: center;

                &--is-header {
                    font-size: 1.6rem;
                    margin: 2rem 0 0;
                    font-weight: bold;
                    color: var(--text-prominent);
                }
            }
            &__description {
                margin-top: 2.4rem;
                @include typeface(--paragraph-center-normal-prominent);
                @include mobile {
                    text-align: center;
                }
            }
            &__container {
                width: 100%;
                @include mobile {
                    overflow-x: hidden;
                    overflow-y: auto;

                    &--no-top-margin {
                        margin-top: 0;
                    }
                }
            }
            &__button {
                margin-top: 1.6rem;
            }
            @include mobile {
                &--deriv-account {
                    border-top: 2px solid var(--general-section-1);
                    padding-top: 1rem;

                    .dc-btn:last-child {
                        width: 90%;
                        margin: 0 auto;
                    }
                }
            }
        }
    }
    &__body {
        display: flex;

        & .details-form {
            @include tablet {
                grid-template-rows: minmax(10rem, 1fr);
            }
        }
    }
    &__set-currency {
        p {
            margin: 2.4rem 0;
            @include typeface(--paragraph-center-normal-black);
            text-transform: initial;
            color: var(--text-prominent);

            @include mobile {
                font-size: 1.2rem;
                margin: 2.4rem auto 0 2.4rem;
                text-align: left;
            }
        }
        h2 {
            @include mobile {
                font-size: 1.2rem;
                text-align: left;
                margin-left: 2.4rem;
            }
        }
    }
    .checkbox {
        align-items: center;
        margin: 1rem 0 0;

        &__box--active {
            background-color: var(--brand-red-coral);
        }
        &__box--active:after {
            font-size: 1.3rem;
            line-height: 2rem;
        }
        &__label {
            @include typeface(--paragraph-left-normal-black);
            text-transform: none;
        }
    }
    &--disabled {
        position: relative;

        .currency-list__items {
            margin-top: 2.4rem;
        }
        .currency-list__item {
            pointer-events: none;

            svg {
                opacity: 0.08;
            }
            .label {
                color: var(--text-disabled);
            }
        }
        &-message {
            display: flex;
            justify-content: center;

            &-description {
                margin-top: 2.4rem;
                width: 78%;
                background-color: var(--status-warning-transparent);
                padding: 0.8rem 0;
                border-radius: $BORDER_RADIUS;
            }
        }
    }
    @include mobile {
        &--error {
            margin-top: $MOBILE_HEADER_HEIGHT;
            display: flex;
            flex-direction: column;
            align-items: center;

            &__restricted-country-error,
            &__invalid-input-error {
                margin: auto 2.4rem;
                height: calc(100vh - 84px);
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 0.8rem;

                .dc-btn {
                    margin-top: 1.9rem;
                    width: 100%;
                }
                .invalid_fields_input {
                    display: list-item;
                    margin: 0.5rem auto;
                    list-style: disc;
                    list-style-position: inside;
                }
            }

            h1 {
                font-size: 2.4rem;
                text-align: center;
                font-weight: bold;
                line-height: 1.5;
                color: var(--text-prominent);
            }
            p {
                font-size: 1.4rem;
                margin: 1.6rem;
                line-height: 1.4;
                color: var(--text-general);
            }
            .dc-btn {
                height: 4rem;
            }
            .dc-ion {
                width: 100%;
                max-width: 9.6rem;
                margin-top: 2rem;
                height: auto;
            }
        }
        &__header {
            &-steps {
                padding: 1.6rem 0 0;
                width: 100%;

                &-title {
                    padding: 0 1.6rem 0.8rem;
                }
                &-subtitle {
                    padding: 1.6rem 1.6rem 0;
                }
            }
        }
    }
}

.add-crypto-currency,
.change-currency {
    height: 100%;

    &__title {
        margin-top: 3rem;
    }
    &__sub-title {
        margin-bottom: 1rem;
    }
